<template>
  <view class="custom-navbar">
    <!-- 渐变背景层 -->
    <view class="navbar-gradient-bg"></view>
    <!-- 状态栏占位 -->
    <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
    <!-- 导航栏内容 -->
    <view class="navbar-content" :style="{ height: navBarHeight + 'px' }">
      <!-- 左侧按钮 -->
      <view class="navbar-left" v-if="showBack || leftIcon">
        <view class="navbar-btn" @click="handleLeftClick">
          <text v-if="showBack" class="iconfont icon-back">‹</text>
          <image v-else-if="leftIcon" :src="leftIcon" class="navbar-icon" mode="aspectFit" />
        </view>
      </view>
      
      <!-- 中间标题 -->
      <view class="navbar-title">
        <text class="title-text">{{ title }}</text>
      </view>
      
      <!-- 右侧按钮 -->
      <view class="navbar-right" v-if="rightIcon || rightText">
        <view class="navbar-btn" @click="handleRightClick">
          <image v-if="rightIcon" :src="rightIcon" class="navbar-icon" mode="aspectFit" />
          <text v-else-if="rightText" class="navbar-text">{{ rightText }}</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'CustomNavbar',
  props: {
    title: {
      type: String,
      default: ''
    },
    showBack: {
      type: Boolean,
      default: true
    },
    leftIcon: {
      type: String,
      default: ''
    },
    rightIcon: {
      type: String,
      default: ''
    },
    rightText: {
      type: String,
      default: ''
    },
    backgroundColor: {
      type: String,
      default: '#F8F0FC'
    }
  },
  data() {
    return {
      statusBarHeight: 0,
      navBarHeight: 44
    };
  },
  computed: {
    totalHeight() {
      return this.statusBarHeight + this.navBarHeight;
    }
  },
  mounted() {
    this.getSystemInfo();
  },
  methods: {
    getSystemInfo() {
      const systemInfo = uni.getSystemInfoSync();
      this.statusBarHeight = systemInfo.statusBarHeight || 0;
      
      // 判断是否为 iPhone X 系列
      const isIPhoneX = /iphone/gi.test(systemInfo.system) && systemInfo.screenHeight >= 812;
      if (isIPhoneX) {
        this.navBarHeight = 44;
      }
    },
    handleLeftClick() {
      if (this.showBack) {
        uni.navigateBack();
      }
      this.$emit('leftClick');
    },
    handleRightClick() {
      this.$emit('rightClick');
    }
  }
};
</script>

<style scoped>
.custom-navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  background-color: #F8F0FC;
  overflow: hidden;
}

.navbar-gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 200%;
  background: linear-gradient(180deg, #F8F0FC 0%, #F5E8FF 50%, #F8F0FC 100%);
  /* 兼容性写法 */
  background: -webkit-linear-gradient(top, #F8F0FC 0%, #F5E8FF 50%, #F8F0FC 100%);
}

.status-bar {
  width: 100%;
  background: transparent;
  position: relative;
  z-index: 1;
}

.navbar-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30rpx;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.navbar-left,
.navbar-right {
  width: 80rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60rpx;
  height: 60rpx;
  cursor: pointer;
}

.icon-back {
  font-size: 48rpx;
  color: #333333;
  font-weight: bold;
  line-height: 1;
}

.navbar-icon {
  width: 44rpx;
  height: 44rpx;
}

.navbar-text {
  font-size: 28rpx;
  color: #333333;
}

.navbar-title {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.title-text {
  font-size: 36rpx;
  font-weight: 600;
  color: #333333;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 500rpx;
}
</style>

